.tile-directions, .tile-help {
  text-align: center;
  padding: 16px;
}

.tile-directions {
  font-size: 1.25em;
}

.tile-help {
  color: var(--text_secondary);
}

.tile-button {
  margin: 8px;
  display: inline-block;
  width: 100px;
  height: 100px;
  background-size: contain;
  border-radius: 4px;

  /* Reset default button styles */
  border: none;
  outline: inherit;
}

.tile-row {
  display: flex;
  justify-content: center;
}

.map-node--available {
  fill: white;
  stroke: #6991bd;
}

.map-node--available:hover,
.map-node--available:focus {
  fill: #dbe3eb;
}

.map-node--completed {
  fill: #9eb7d1;
  stroke: #587fa6;
}

.map-node--locked {
  stroke: #aeb8c2;
  fill: #ecf0f3;
}

.button-row > button:not(:last-child) {
  margin-right: 8px;
}

.monster-sprite {
  display: block;
  margin: 0 auto;
}

